import React , {useState} from 'react';
import { Container, Typography, IconButton, InputAdornment, OutlinedInput, InputLabel, FormControl, Button, colors, Paper, CssBaseline } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ResetPasswordImg from 'src/components/ResetPasswordImg';
import { useDispatch } from 'react-redux';
import { useSnackbar } from 'notistack';
import { yupResolver } from '@hookform/resolvers';
import * as yup from 'yup';
import { Visibility, VisibilityOff } from "@material-ui/icons";
import { Controller, useForm } from 'react-hook-form';
import { resetPasswordAction } from 'src/actions/accountActions';
import { useHistory } from 'react-router-dom';
import {useTranslation} from "react-i18next";
import { TRANSLATE_NS } from 'src/constants'

const useStyles = makeStyles(theme => ({
  paper: {
    paddingTop: '3rem',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  text__header: {
    marginTop: theme.spacing(6),
  },
  btn__action: {
    marginTop: theme.spacing(2),
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  errorWrapper: {
    backgroundColor: colors.red[100],
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0)
  },
}));

const ResetPassword = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const history = useHistory();
  const {t} = useTranslation([TRANSLATE_NS.TRANSLATIONS]);

  const [showNewPassword, setShowNewPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  const [result, setResult] = useState(null)

  const handleClickShowNewPassword = () => {
    setShowNewPassword(prev=> !prev);
  }

  const handleClickShowConfirmPassword = () => {
    setShowConfirmPassword(prev=> !prev);
  }

  const handleMouseDownNewPassword = (event) => {
    event.preventDefault();
  }

  const handleMouseDownConfirmPassword = (event) => {
    event.preventDefault();
  }
  const hasError = inputName => !!(errors && errors[inputName])
  const errorMessage = inputName => errors[inputName]?.message ?? null

  const schemaValidationPassword = yup.object().shape({
    newPassword: yup.string()
    .required(t('translations:required'))
    .min(8, t('translations:please enter at least', {minChar:'8'})),
    confirmPassword: yup.string()
    .required(t('translations:required'))
    .min(8, t('translations:please enter at least', {minChar:'8'}))
    .test('passwords-match', (t('translations:passwords do not match')), function (value) {
      return this.parent.newPassword === value;
    }),
  })
  const { control, errors, handleSubmit, formState } = useForm({
    mode: "onChange",
    defaultValues: {
      newPassword: '',
      confirmPassword: ''
    },  
    resolver: yupResolver(schemaValidationPassword)
  })

  const { isSubmitting, isValid } = formState

  const onSubmit = handleSubmit(async (data) => {
    try{
      console.log('isValid: ', isValid);
      if(!isValid){
        return;
      }

      const { newPassword, confirmPassword } = data;
      await dispatch(resetPasswordAction(newPassword, confirmPassword))
      enqueueSnackbar(t('translations:password changed successfully'), {
        variant: 'success'
      });
      history.push('/')
    }
    catch(error){
      console.log(error);
      setResult(()=> error);
    }
  })

  return(
    <Container component="main" maxWidth="xs">
    <CssBaseline />
      <div className={classes.paper}>
      <ResetPasswordImg/>
        <Typography variant="h3" className={classes.text__header}>
        {t('translations:reset your password')}
        </Typography>
        
        <form className={classes.form} noValidate onSubmit={onSubmit}>
        {/* Error form API */}
        {result && (
            <Paper 
              elevation={0}
              className={classes.errorWrapper}
            >
              <Typography variant="body2" color="error">
                {result?.data?.message}
              </Typography>
            </Paper>
          )}

        {/*New Password Part*/}
          <Controller
            name={"newPassword"}            
            control={control}
            render={(props) => (
              <FormControl
                variant="outlined"
                margin="normal"
                fullWidth
                label={t('translations:new password')}
                type="password"
                autoComplete="new-password"
                {...props}                
              >
                <InputLabel htmlFor="outlined-adornment-new-password">{t('translations:new password')}</InputLabel>
                <OutlinedInput
                  id="outlined-adornment-new-password"
                  type={showNewPassword ? "text" : "password"}
                  value={props.value}                  
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowNewPassword}
                        onMouseDown={handleMouseDownNewPassword}
                        edge="end"
                      >
                        {showNewPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  }
                  labelWidth={115}
                />
              </FormControl>
            )}
          />
          {/*Error for New Password Part*/}
          {hasError("newPassword") && (
            <div>
              <Typography variant="body2" color="error">
                {errorMessage("newPassword")}
              </Typography>
            </div>
          )}

          {/*Confirm Password Part*/}
          <Controller
            name={"confirmPassword"}            
            control={control}
            render={(props) => (
              <FormControl
                variant="outlined"
                margin="normal"
                fullWidth
                label={t('translations:confirm password')}
                type="password"
                autoComplete="confirm-password"
                {...props}                
              >
                <InputLabel htmlFor="outlined-adornment-confirm-password">{t('translations:confirm password')}</InputLabel>
                <OutlinedInput
                  id="outlined-adornment-confirm-password"
                  type={showConfirmPassword ? "text" : "password"}
                  value={props.value}                  
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowConfirmPassword}
                        onMouseDown={handleMouseDownConfirmPassword}
                        edge="end"
                      >
                        {showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  }
                  labelWidth={145}
                />
              </FormControl>
            )}
          />
          {/*Error for Confirm Password part*/}
          {hasError("confirmPassword") && (
            <div>
              <Typography variant="body2" color="error">
                {errorMessage("confirmPassword")}
              </Typography>
            </div>
          )}

          {/* Submit Button */}
          <Button
            disabled={isSubmitting}
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.btn__action}
          >
            {t('translations:submit')}
          </Button>

        </form>
      </div>
    </Container>
  );
};

export default ResetPassword;